<template>
  <el-container>
    <!-- 头部 -->
    <el-header>
      <div>
        <img src="../assets/heima.png" alt class="home_header_icon" />
        <span>后台电商管理系统</span>
      </div>
      <div>
        <el-button type="info" @click="dropOut">退出</el-button>
      </div>
    </el-header>
    <el-container>
      <!-- 侧边栏 -->
      <el-aside :width="showActive?'64px':'200px'">
        <div class="home_aside_activeWidth" @click="activeWidth">|||</div>
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          :collapse="showActive"
          router
          :collapse-transition="false"
          :default-active="activePath"
        >
          <!-- 一级菜单 -->
          <el-submenu :index="item.id+''" v-for="item in asideList" :key="item.id">
            <template slot="title">
              <i :class="iconList[item.id]" class="home_aside_icon"></i>
              <span>{{item.authName}}</span>
            </template>
            <el-menu-item-group>
              <!-- 二级菜单 -->
              <el-menu-item
                :index="'/'+items.path"
                v-for="items in item.children"
                :key="items.id"
                @click="subPath('/'+items.path)"
              >
                <i class="el-icon-menu"></i>
                <span>{{items.authName}}</span>
              </el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!-- 主要内容 -->
      <el-main>
        <router-view></router-view>
        
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      asideList: [],
      iconList: {
        '125': 'iconfont icon-users',
        '103': 'iconfont icon-tijikongjian',
        '101': 'iconfont icon-shangpin',
        '102': 'iconfont icon-danju',
        '145': 'iconfont icon-baobiao',
      },
      showActive: false,
      activePath: '',
    }
  },
  methods: {
    dropOut() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    async asideItems() {
      const { data: res } = await this.$http.get('menus')
      // window.console.log(res)
      this.asideList = res.data
      // window.console.log(this.asideList)
    },
    activeWidth() {
      this.showActive = !this.showActive
    },
    subPath(activePath) {
      // window.console.log(activePath)
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    },
  },
  mounted() {
    this.asideItems()
    this.activePath = window.sessionStorage.getItem('activePath')
  },
}
</script>

<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  /* text-align: center; */
  /*   */
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.el-aside {
  background-color: #333744;
  color: #333;
  height: 100vh;
  /* text-align: center;
    line-height: 200px; */
}
.el-menu {
  border-right: 0;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  /* text-align: center;
    line-height: 160px; */
}
.home_header_icon {
  width: 45px;
  height: 45px;
  /* margin-top:20px; */
  vertical-align: middle;
  margin-right: 10px;
}
.home_aside_icon {
  margin-right: 10px;
  vertical-align: middle;
}
.home_aside_activeWidth {
  color: white;
  background: #333744;
  letter-spacing: 5px;
  display: flex;
  justify-content: center;
  margin: 10px auto;
  font-size: 10px;
  cursor: pointer;
}
</style>